*,
::before,
::after {
    padding: 0;
    margin: 0;
    outline: 0;
    border: 0;
    background: none;
    box-sizing: border-box;
    font-family: inherit;
    font-weight: normal;
    -webkit-tap-highlight-color: transparent;
}

html {
    font-family: "BMJUA", sans-serif;
}

a {
    color: #000;
    text-decoration: none;
}

a:hover {
    color: #aaa;
}

.flex {
    display: flex;
}

.center {
    text-align: center;
}

.flex.center {
    align-items: center;
    justify-content: center;
}

/* container */
#main > section {
    margin: 0 auto;
}

body:not(.hideHeader) #main > section:not(#home) {
    margin-top: 140px;
}

#main > section.max-1400 {
    max-width: 1400px;
}
